/***/ (function(module, exports) {

	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"T and A Politics","date":"12 Aug 2009","slug":"/2009-08-12-t-and-a-politics/","tags":"Y2009"},"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2009/08/t.jpg\" alt=\"t.jpg\" border=\"0\" width=\"225\" height=\"300\" /> <img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2009/08/a.jpg\" alt=\"a.jpg\" border=\"0\" width=\"225\" height=\"325\" /></p>\n<p>I'm sorry, but when I saw these two posters yesterday, I knew they just were begging to be combined in a single post. The poster on the right has managed to hit a taboo-trifecta (race, nudity, homosexualtiy) with maybe even more taboos I haven't noticed (bad nail polish?).</p>\n<p>Two great tastes that go great... no, I won't finish that thought.</p>\n<p>(Left: Berlin CDU Bundestag candidate Vera Lengsfeld, \"We have more to offer\", seen at <a href=\"http://50percentdna.blogspot.com/2009/08/merkel.html\">50% of my DNA</a>. Right: Local Green poster in Kaarst, NRW, \"The only reason to vote black\", seen at <a href=\"http://www.amiexpat.com/2009/08/11/racist-tasteless-or-provocative/\">AmiExpat</a>)</p>"},"next":{"fields":{"title":"Na?","date":"10 Aug 2009","slug":"/2009-08-10-na/"}},"previous":{"fields":{"title":"My Health Insurance and Welcome to It","date":"14 Aug 2009","slug":"/2009-08-14-my-health-insurance-and-welcome-to-it/"}}},{"node":{"fields":{"title":"Na?","date":"10 Aug 2009","slug":"/2009-08-10-na/","tags":"Y2009"},"html":"<p>The past two weeks these posters have been sprouting like mushrooms.</p>\n<p><a href=\"http://www.flickr.com/photos/51035717986@N01/3808364680\" title=\"View 'IMG_0236' on Flickr.com\"><img src=\"https://farm3.static.flickr.com/2638/3808364680_cdea85c1f6.jpg\" alt=\"IMG_0236\" border=\"0\" width=\"375\" height=\"500\" /></a></p>\n<p>What do they mean? I've gathered the following facts:</p>\n<ol>\n<li>Federal elections are in 6 weeks</li>\n<li>The background color is SPD red</li>\n<li>The back of the poster is stamped with \"SPD Harburg\"</li>\n</ol>\n<p>So the posters have something to do with the SPD. The local paper explained the mystery that the posters will be updated twice before the election with more letters to form a complete sentence. I would think, though, with the bad polls the SPD has been getting, they'd be better off getting their complete message out now rather than waiting until later. By then it might be too late.</p>\n<p>Then we have this. I take it the graphic artist is a plant from the opposing party and deliberately sabotaged this poster.</p>\n<p><a href=\"http://www.flickr.com/photos/51035717986@N01/3808362052\" title=\"View 'IMG_0235' on Flickr.com\"><img src=\"https://farm3.static.flickr.com/2545/3808362052_d00fcc3a63.jpg\" alt=\"IMG_0235\" border=\"0\" width=\"375\" height=\"500\" /></a></p>\n<p>On the other hand, there is the \"... na\" from the first poster. That mystery is now solved! But what is <a href=\"http://www.monika-griefahn.de/\">the candidate's last name</a> again, in case we wanted to vote for her?</p>"},"next":{"fields":{"title":"Why PapaScott?","date":"08 Aug 2009","slug":"/2009-08-08-why-papascott/"}},"previous":{"fields":{"title":"T and A Politics","date":"12 Aug 2009","slug":"/2009-08-12-t-and-a-politics/"}}},{"node":{"fields":{"title":"Why PapaScott?","date":"08 Aug 2009","slug":"/2009-08-08-why-papascott/","tags":"Y2009"},"html":"<p>Why PapaScott?</p>\n<p>The oldest remaining shards of what would be become PapaScott date back 10 years to <a href=\"/blogger99.html\">August 1999</a> and were posted with <a href=\"http://www.blogger.com/\">Blogger</a>, a shiny new technology at the time.</p>\n<p>In 1999 we were pregnant with Christopher, and we were calling each other cute names like MamaMaus and PapaScott. And when I <a href=\"http://www.papascott.de/archives/1999/12/05/it-worked/\">started</a> a site on the brand spaning new <a href=\"http://www.scripting.com/davenet/1999/12/08/editthispagecom.html\">edithispage.com</a>, it didn't take long for me to start calling it PapaScott, even after Christopher was <a href=\"http://www.papascott.de/archives/1999/12/27/christopher-ryan-hanson/\">born</a> later that month. The name fits, even today.</p>\n<p>So why PapaScott? Why not?</p>"},"next":{"fields":{"title":"Nine Azubis","date":"03 Aug 2009","slug":"/2009-08-03-nine-azubis/"}},"previous":{"fields":{"title":"Na?","date":"10 Aug 2009","slug":"/2009-08-10-na/"}}}],"pathPrefix":"","first":false,"last":false,"index":53,"pageCount":1101}}

/***/ })

